export function number_formate (number, point = true, decimals = 2, dec_point = '.', thousands_sep = ',') {
  /*
   * 参数说明：
   * number：要格式化的数字
   * decimals：保留几位小数
   * dec_point：小数点符号
   * thousands_sep：千分位符号
   * */
  if (point) {
    number = insertStr(number)
  }
  return (number.toFixed(2) + '').replace(/\d{1,3}(?=(\d{3})+(\.\d*)?$)/g, '$&,');
}
function insertStr (number) {
  return (number / 100)
}
