<template>
  <div class="cgi-home">
    <div class="flex flex-coloum" style="width:600px" v-if="userInfo">
      <div class="left-card  mb-30">
        <div class="flex-ac">
          <div class="circle-icon mr-10"> <img src="~@/assets/images/user.png" alt=""></div>
          <span>{{userInfo.phone_num}}</span>
        </div>
        <div class="line"></div>
        <div class="flex-ac">
          <div class="circle-icon mr-10">
            <img src="~@/assets/images/money.png" alt="">
          </div>
          <span>账户余额：</span><span class="f20">{{useBalance}}</span>
        </div>
        <div class="line"></div>
        <Button type="primary" class="chargeBtn" @click.native="toCharge">充值</Button>
      </div>
      <div class="r-card">
        <div class="r-card-l">
          <img src="~@/assets/images/yonghu.png" alt="">
        </div>
        <div class="r-card-content">
          <div class="card-t ">
            <div class="flex-ac">
              <label class="f20 label">商户名:</label>
              <Input type="text" style="width:200px;" v-model="name" v-if="userInfo.name==''||update" />
              <span class="f20" v-else>{{userInfo.name}}</span>
            </div>
          </div>
          <div class="flex1">
            <label class="f20 label">邮箱:</label>
            <Input type="text" style="width:200px;" v-model="email" v-if="userInfo.email==''||update" />
            <span class="f20" v-else>{{userInfo.email}}</span>
          </div>
          <Button class="btnpos" type="primary" v-if="update||userInfo.email==''" @click.native="handleClick">
            保存
          </Button>
          <Button class="btnpos" v-else @click="update=true">修改</Button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import localstore from '@/utils/localstore.js'
import { mapGetters } from 'vuex'
import { number_formate } from '@/utils/number_formate.js'
export default {
  computed: {
    ...mapGetters(['userInfo']),
    useBalance () {
      return number_formate((this.userInfo.balance - this.userInfo.deposit))
    }
  },
  mounted () {
    this.operations = localstore.get('operations')
    this.name = this.userInfo ? this.userInfo.name : ''
    this.email = this.userInfo ? this.userInfo.email : ''
  },
  data () {
    return {
      operations: [],
      update: false,
      name: '',
      email: ''
    }
  },
  methods: {
    async handleClick () {
      if (!this.name) {
        return this.$Message.error('请输入商户名')
      }
      if (!/^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/.test(this.email)) {
        return this.$Message.error('邮箱格式输入不正确')
      }
      if (this.name && this.email) {
        let params = {
          id: this.userInfo.id,
          name: this.name,
          email: this.email
        }
        let resData = await this.$post('cgi/set_mchinfo', params)
        if (resData.ret_code == 0) {
          this.$store.dispatch('user/submitUserInfo')
          this.update = false
        }
      }
    },
    toCharge () {
      this.$router.push('/charge')
    }
  }
}
</script>
<style lang="less" scoped>
.cgi-home {
  .left-card {
    height: 175px;
    border-radius: 20px;
    display: flex;
    font-size: 18px;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    padding: 0 22px;
  }
  .r-card {
    display: flex;
    height: 175px;
    border-radius: 20px;
    overflow: hidden;
    background-color: #fff;
    .r-card-l {
      width: 89px;
      display: flex;
      background-color: #5cadff;
      color: #fff;
      align-items: center;
      justify-content: center;
    }
    .r-card-content {
      padding: 0 18px;
      display: flex;
      flex: 1;
      position: relative;
      flex-direction: column;
      .btnpos {
        position: absolute;
        top: 20px;
        right: 20px;
      }
      .label {
        width: 80px;
      }
      .card-t {
        display: flex;
        flex: 1;
        align-items: flex-end;
        padding-bottom: 20px;
        margin-bottom: 20px;
        border-bottom: 1px solid #c9c9c9;
      }
    }
  }
  .circle-icon {
    width: 45px;
    height: 45px;
    border-radius: 50%;
  }
  .line {
    height: 28px;
    width: 1px;
    opacity: 0.43;
    background-color: #bfbfbf;
  }
  .chargeBtn {
    width: 88px;
    height: 37px;
    border-radius: 20px;
  }
}
</style>